import React, { Component } from 'react';
import { Table, Select } from "antd";
import { connect } from "react-redux";
import { actionCreators } from './store';
import { Link } from "react-router-dom";
import Button from '../../../common/components/Button';

const { Option } = Select;

class Kefu extends Component {

  state = {
    messageType: ""
  };

  UNSAFE_componentWillMount () {
    this.props.getKefuMessageList()
  }

  render() {
    const { kefuMessageList, copyMessage, deleteMessage, sendKefuMessage, messageType, handleChangeType }  = this.props;
    const kefuMessageHeader = [
      {
        key: "remarks",
        title: "",
        dataIndex: "remarks",
        align: "center",
        render: text => !text ? "" : text
      },
      {
        key: "delay",
        title: "()",
        align: "center",
        width: 120,
        render: (text, record) => record.task ? record.delay : ""
      },
      {
        key: "timing_time",
        title: "()",
        align: "center",
        sorter: (a, b) => (new Date(a.timing_time)).getTime() - (new Date(b.timing_time)).getTime(),
        render: (text, record) => record.is_timing ? record.time : ""
      },
      {
        key: "gonghaoList",
        dataIndex: "gonghaoList",
        title: " (4)",
        align: "center",
        width: 100,
        render: text => text.slice(0, 4).map(item => <p key={item._id}>{item.nick_name}</p>)
      },
      {
        key: "sex",
        title: "",
        align: "center",
        dataIndex: "sex",
        width: 120,
        render: text => text === "0" ? "" : text === "1"  ? "" : text === "2" ? "" : ""
      },
      {
        key: "action_type",
        title: "",
        align: "center",
        dataIndex: "action_type",
        width: 100,
        render: text => text === 0 ? "" : text === 3  ? "" : text === 2 ? "" : ""
      },
      {
        title: "Action",
        align: "center",
        width: 280,
        render: (text, record, index) => (
          <div>
            <Button background="success" onClick={() => copyMessage(record)} buttonContent="" />
            <Button buttonContent={<Link to={{pathname: "./update", query: {...record}}}></Link>} />
            <Button disabled={record.is_timing || record.delay} background={(record.is_timing || record.delay) ? "info" : "warning"} onClick={() => sendKefuMessage(record._id, record.tagId)} buttonContent="" />
            <Button background="danger" onClick={() => deleteMessage(record._id, index)} buttonContent="" />
          </div>
        )
      }
    ];
    return (
    	<div>
        <div className="container">
          <Button size="default" mb={20} buttonContent={<Link to="./create"></Link>} />
          <Select value={messageType} onChange={handleChangeType}>
            <Option value={3}></Option>
            <Option value={0}></Option>
            <Option value={1}></Option>
            <Option value={2}></Option>
          </Select>
          <Table size="middle" rowKey={record => record._id} columns={kefuMessageHeader} dataSource={kefuMessageList} />
        </div>
      </div>
    )
  }
}

const mapState = state => ({
  kefuMessageList: state.message.kefu.kefuMessageList,
  messageType: state.message.kefu.messageType
});

const mapDispatch = dispatch => ({
  getKefuMessageList() {
    dispatch(actionCreators.getKefuMessageList())
  },
  copyMessage(record) {
    dispatch(actionCreators.createKefuMessage(record))
  },
  deleteMessage(id, index) {
    dispatch(actionCreators.deleteMessage(id, index))
  },
  sendKefuMessage(id, tagId) {
    dispatch(actionCreators.sendKefuMessage(id, tagId))
  },
  handleChangeType(value) {
    dispatch(actionCreators.handleChangeType(value))
  }
});

export default connect(mapState, mapDispatch)(Kefu);